













import {Component, Prop, Vue} from "vue-property-decorator";

@Component
export default class ChatMessage extends Vue{
  @Prop({default: false}) me: boolean | undefined;
  @Prop({ default: '' })  readonly message: string | undefined;
  @Prop({ default: '' }) readonly name: string | undefined;
  @Prop({ default: 'repartidor' }) readonly role: string | undefined;
  @Prop({ default: null }) readonly imageUrl: string | null | undefined;

}
